.item {
    position: absolute;
    width: 100px;
    height: 100px;
    /*overflow: hidden;*/
    /*
    linear:       the transition will have constant speed from start to end.
    ease:         the transition will start slowly, then get faster, before ending slowly.
    ease-in:      the transition will start slowly.
    ease-out:     the transition will end slowly.
    ease-in-out:  the transition starts and ends slowly.
    cubic-bezier: define specific values for your own transition.
    */
    /** Chrome & Safari **/
    /** Firefox **/
    /** Opera **/
    /** IE **/
    transition: all 500ms ease-in-out;
    z-index: 10;
}
.item .img {
    position: absolute;
    z-index: 100;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid #eee;
    background-size: 150%;
    background-position: 50% 50%;
    transform: rotate(10deg);
    -ms-transform: rotate(10deg);
    /* IE 9 */
    -webkit-transform: rotate(10deg);
    /* Safari and Chrome */
    -o-transform: rotate(10deg);
    /* Opera */
    -moz-transform: rotate(10deg);
    /* Firefox */
    /** Chrome & Safari **/
    /** Firefox **/
    /** Opera **/
    /** IE **/
    transition: all 500ms ease-in-out;
}

.item:hover .img {
    background-size: 100%;
    background-position: 50% 50%;
    transform: rotate(0);
    -ms-transform: rotate(0);
    /* IE 9 */
    -webkit-transform: rotate(0);
    /* Safari and Chrome */
    -o-transform: rotate(0);
    /* Opera */
    -moz-transform: rotate(0);
    /* Firefox */
    border-radius: 0;
    -moz-background-clip: padding;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    position: absolute;
    z-index: 100;
}

.item:hover {
    z-index: 100;
}

.item .info {
    font-family: 'Open Sans', sans-serif;
    width: 300px;
    padding: 20px;
    background: rgba(0, 0, 0, 0.7);
    color: #FBFBFF;
    font-size: 14px;
    /* display: none; */
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    /* IE 9 */
    -webkit-transform: rotate(90deg);
    /* Safari and Chrome */
    -o-transform: rotate(90deg);
    /* Opera */
    -moz-transform: rotate(90deg);
    /* Firefox */
    position: absolute;
    top: 300px;
    /* right: -340px; */
    z-index: 1;
    opacity: 0;
    /** Chrome & Safari **/
    /** Firefox **/
    /** Opera **/
    /** IE **/
    transition: all 500ms ease-in-out;
}

.item .info.left {
    top: 0;
    top: 300px;
    /* left: -340px; */
    -webkit-transform-origin: 100% 0;
    transform-origin: 100% 0;
    transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    /* IE 9 */
    -webkit-transform: rotate(-90deg);
    /* Safari and Chrome */
    -o-transform: rotate(-90deg);
    /* Opera */
    -moz-transform: rotate(-90deg);
    /* Firefox */
}

.item:hover .info {
    display: block;
    transform: rotate(0);
    -ms-transform: rotate(0);
    /* IE 9 */
    -webkit-transform: rotate(0);
    /* Safari and Chrome */
    -o-transform: rotate(0);
    /* Opera */
    -moz-transform: rotate(0);
    /* Firefox */
    opacity: 1;
    position: absolute;
    z-index: 1;
    transition-delay: 500ms;
    -moz-transition-delay: 500ms;
    /* Firefox 4 */
    -webkit-transition-delay: 500ms;
    /* Safari and Chrome */
    -o-transition-delay: 500ms;
    /* Opera */
}


/* Buttons */
.btn {
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
        appearance: none;
    background-color: transparent;
    border: 2px solid #D5573B;
    border-radius: 0.6em;
    color: #D5573B;
    cursor: pointer;
    display: flex;
    align-self: center;
    font-size: .7rem;
    font-weight: 400;
    line-height: 1;
    margin: 20px;
    padding: 1.2em 2.8em;
    text-decoration: none;
    text-align: center;
    text-transform: uppercase;
    font-family: 'Open Sans', sans-serif;
    font-weight: 700;
}

.btn:hover, .btn:focus {
    color: #FBFBFF;
    outline: 0;
}

.fill {
    transition: box-shadow 300ms ease-in-out, color 300ms ease-in-out;
}

.fill:hover {
    box-shadow: 0 0 40px 40px #D5573B inset;
}

